.pokedex {
  flex: 1 1 100%;
  padding: 0 40px 0;
  display: flex;
  flex-direction: column;
}
.pokedex .pokedex__container {
  padding: 140px 40px 40px;
  background: white;
  flex: 1 1 100%;
  width: 100%;
  max-width: 1420px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
}
.pokedex .pokedex__container .pokedex__pokemon-card-list {
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-gap: 40px;
  justify-content: center;
  margin-bottom: 40px;
}
.pokedex .pokedex__container .pokedex__load-button {
  padding: 20px;
  justify-self: flex-end;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 20px;
  font-weight: 800;
  color: white;
  background: #53a4cf;
  transform: scale(1);
  transition: 0.1s ease-in-out transform;
}
.pokedex .pokedex__container .pokedex__load-button.hidden {
  transform: scale(0);
}
.pokedex .pokedex__container .pokedex__load-button .button_text {
  margin-right: 10px;
}
.pokedex .pokedex__container .pokedex__load-button svg {
  width: 30px;
  height: 30px;
}

@media screen and (max-width: 767.98px) {
  .pokedex {
    padding: 20px;
  }
  .pokedex .pokedex__container {
    padding: 10px;
  }
}
@media screen and (max-width: 499.98px) {
  .pokedex {
    padding: 0px;
  }
  .pokedex .pokedex__container {
    padding: 10px;
  }
}

