@charset "UTF-8";
/*Обнуление*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  scroll-behavior: smooth;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

/*--------------------*/
.wrapper {
  font-family: Roboto, sans-serif;
  background: url("./images/body_bg.jpg");
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
.wrapper .main {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 767.98px) {
  .wrapper .main .pokedex {
    padding: 20px;
  }
  .wrapper .main .pokedex .pokedex__container {
    padding: 10px;
  }
}
@media screen and (max-width: 499.98px) {
  .wrapper .main .pokedex {
    padding: 0px;
  }
  .wrapper .main .pokedex .pokedex__container {
    padding: 10px;
  }
}

